.rbpdf{
    margin-left: 15vw;
    width: 52.5vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

.rbpdf iframe{
    width: 50vw;
    box-shadow: 0 0 2px #000;
    height: 70vh;
}

.pdfv{
    padding: 1vw;
    width: 70vw;
    background: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.pdfv img{
    margin-bottom: 1vw;
}