.aBookPage{
    background: #fff;
    padding: 0.8rem;
    margin-left: 15vw;
    display: flex;
    justify-content: space-between;
    width: 80vw;
    margin-top: -1.5rem;
    height: 77vh;
}

.aBookPage img{
    width: 13rem;
}

.left{
    width: 55vw;
    position: relative;
}

#top{
    display: flex;
}

.left p{
    font-size: 1.3rem;
    font-family: Roboto Medium;
    margin-top: 1rem;
}

#top .bookList{
    display: flex;
    align-items: center;
}

.scroll{
    display: none;
}

#top #change{
    outline: none;
    border: none;
    background: #f00;
    border-radius: 5px;
    font-family: Roboto Medium;
    font-size: 1rem;
    color: #fff;
    height: 2.5rem;
    margin-left: 1rem;
    padding: 0.5rem 1.5rem;
}

#top #content{
    padding-left: 1rem;
}

#top h1{
    font-size: 2.5rem;
    font-family: Roboto Medium;
}

#startReading{
    outline: none;
    border: none;
    background: #005500;
    border-radius: 5px;
    font-family: Roboto Medium;
    font-size: 1.5rem;
    color: #fff;
    padding: 0.5rem 1.5rem;
}
#editBook{
    outline: none;
    border: none;
    background: #000055;
    border-radius: 5px;
    font-family: Roboto Medium;
    font-size: 1.5rem;
    color: #fff;
    padding: 0.5rem 2.9rem;
}

.aBookPage h3{
    font-family: Mulish ExtraLight;
    font-weight: 508;
    letter-spacing: 0.05px;
    margin-top: 1rem;
    color: #555;
    font-size: 1.5rem;
}

.right {
    padding: 1rem;
    width: 22vw;
    text-align: center;
    height: 50vh;
    box-shadow: 0 0 20px #ccc;
}

.right #editBookMetaData{
    outline: none;
    border: none;
    background: #000055;
    border-radius: 5px;
    margin-bottom: 1rem;
    font-family: Roboto Medium;
    font-size: 1.3rem;
    color: #fff;
    padding: 0.5rem 1.5rem;
}

.right h1{
    font-size: 1rem;
    font-family: Roboto Medium;
}

.right #deleteBook{
    outline: none;
    border: none;
    background: #f00;
    border-radius: 5px;
    font-family: Roboto Medium;
    font-size: 1.3rem;
    color: #fff;
    padding: 0.5rem 1.5rem;
}

@media (max-width: 1024px) {
    .aBookPage{
        width: 100vw;
        margin-left: 0;
        margin-top: 3rem;
    }
    .ab{
        height: 75vh;
        margin-bottom: 6rem;
    }
    .aBookPage .ov{
        display: none;
        position: fixed;
        z-index: 99999999999999999999999999;
        background: rgba(255, 255, 255, 0.95);
    }
    .scroll{
        display: block;
        width: 70vw;
        text-align: center;
        position: absolute;
        left: 50%;
        top: 30rem;
        font-family: Roboto Medium;
        transform: translateX(-50%);
    }
    .aBookPage .ov.dbb{
        display: block;
    }
    .aBookPage img{
        height: 10rem;
        width: 7rem;
    }
    .right.d{
        display: none;
    }
    .right{
        position: fixed;
        top: 20%;
        z-index: 9999999999999;
        width: 80vw;
        left: 50%;
        transform: translate(-50%);
    }
    .right h1{
        font-size: 1.1rem;
    }
    .right #editBookMetaData{
        padding: 0.5rem 1.5rem;
        font-size: 1.3rem;
    }
    .right #deleteBook{
        padding: 0.5rem 1.5rem;
        font-size: 1.3rem;
    }
    #top h1{
        font-size: 2rem;
    }
    #top #startReading{
        font-size: 1rem;
        padding: 0.5rem 0.94rem;
    }
    #top #editBook{
        padding: 0.5rem 1.8rem;
        font-size: 1rem;
    }
    .left p{
        font-size: 1rem;
        font-family: Roboto Medium;
        margin-top: 1rem;
    }
    #top .bookList{
        display: flex;
        width: 95vw;
        top: 16rem;
        margin-left: 0;
        left: 0;
        position: absolute;
        align-items: center;
    }
    .aBookPage h3{
        margin-top: 1rem;
        font-family: Roboto Medium;
        font-size: 0.8rem;
        width: 95vw;
    }
}