.ub{
    margin-top: 3rem;
}


.fss{
    position: absolute;
    right: 0rem;
    width: 8rem;
}

.btn-fileee{
    background: #000;
    color: #fff;
    margin-right: 9rem;
    user-select: none;
    padding: 0.5rem 1.5rem;
    border-radius: 2px;
    font-family: Roboto Medium;
}

.editGravatarrr{
    border: 1px solid #fff;
    width: 11rem;
    position: absolute;
    right: 0;
    height: auto;
}

.editGravatar{
    border: 1px solid #fff;
    width: 8rem;
    height: auto;
}

.preview{
    border: 1px solid #fff;
    width: 8rem;
    height: auto;
    position: absolute;
    left: 30rem;
}

.editGravatarr{
    border: 1px solid #fff;
    width: 11rem;
    position: absolute;
    right: 0;
    height: auto;
}

.gravatarComponent{
    width: 52.5vw;
    display: inline;
}

.failed{
    position: absolute;
    left: 0;
    font-size: 1.5rem;
    width: 8rem;
    height: 8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffffdd;
}

.loading{
    border: 1px solid #fff;
    width: 8rem;
    height: auto;
    position: absolute;
    left: 20rem;
}

.preview_btn{
    border: 1px solid #fff;
    width: 8rem;
    height: auto;
    position: absolute;
    clip-path: polygon(0% 20%, 60% 20%, 60% 0%, 100% 50%, 60% 100%, 60% 80%, 0% 80%);
    background: #000;
    text-align: center;
    padding: 1rem 0rem;
    font-size: 1.3rem;
    color: #fff;
    left: 20rem;
}

.btn-file{
    position: absolute;
    background: #000;
    color: #fff;
    user-select: none;
    padding: 0.5rem 1.5rem;
    border-radius: 2px;
    font-family: Roboto Medium;
}

.failedd{
    position: absolute;
    right: 0;
    font-size: 1.5rem;
    width: 8rem;
    height: 8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffffdd;
    box-shadow: 0 0 20px #000;
}

.failed2{
    position: absolute;
    right: -0rem;
    font-size: 1.5rem;
    width: 8rem;
    height: 8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffffdd;
    box-shadow: 0 0 20px #ccc;
}

.gravatarComponent2{
    align-items: center;
    position: relative;
    display: flex;
    justify-content: space-between;
}

.gravatarComponent22{
    position: relative;
    display: flex;
    justify-content: space-between;
}

.popup{
    position: fixed;
    background: #fffffffe;
    width: 100%;
    height: 100vh;
    top: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: Roboto Medium;
    color: #00f;
    animation: fadeOut 5s linear!important;
    opacity: 0;
    z-index: -9999999999999999999999999999999999;
}

.popup h1{
    font-size: 2.5rem;
}

@keyframes fadeOut {
    0%{
        display: block!important;
        opacity: 1;
        z-index: 9999999999999999999999999999999999;
    }
    100%{
        opacity: 0;
        z-index: -9999999999999999999999999999999999;
    }
}

@media (max-width: 1024px){
    .popup h1{
        font-size: 1rem;
    }
    .change_img{
        position: absolute!important;
        bottom: 18rem;
    }
    .editGravatar{
        width: 7rem;
    }
    .preview{
        position: absolute;
        left: 8rem;
        top: 7.5rem;
    }
    .gravatarComponent{
        display: block;
    }
    .gravatarComponent2{
        width: auto;
        display: block;
    }
    .ub{
        position: absolute!important;
        bottom: 20vw!important;
        margin-top: 0;
    }
    .editGravatarrr{
        border: 1px solid #fff;
        width: 11rem;
        position: absolute;
        left: 0;
        bottom: -14rem;
        height: auto;
    }
    .fss{
        width: 5rem;
        font-size: 0.8rem;
        right: 3.5rem;
    }
    .failed2{
        position: absolute;
        left: 0rem;
        font-size: 1.5rem;
        width: 8rem;
        height: 8rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #ffffffdd;
        box-shadow: 0 0 20px #ccc;
        top: 100%;
    }
    .failed{
        position: absolute;
        left: 0rem;
        font-size: 1.5rem;
        width: 8rem;
        height: 8rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #ffffffdd;
        box-shadow: 0 0 20px #ccc;
        top: 100%;
    }
    .preview_btn{
        left: 8rem;
        top: 3rem;
    }
    .loading{
        position: absolute;
        bottom: -6rem;
        left: 0;
        border: none;
    }
    .btn-file{
        margin-left: 0.8rem;
    }
    .btn-fileee{
        padding: 0.5rem 1rem;
    }
    .failedd{
        position: absolute;
        left: 0rem;
        font-size: 1.5rem;
        width: 8rem;
        height: 8rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        background: #ffffffdd;
        box-shadow: 0 0 20px #ccc;
        top: 100%;
    }
    .editProfileForm{
        margin-bottom: 7.5rem;
    }
    .gravatarComponent{
        width: auto;
    }
    .editGravatarr{
        width: 7rem;
        position: relative;
    }
}

@media (min-width: 1025px) {
    .btn-file{
        left: 9rem;
    }
}