}

.cfg2nd{
    display: flex;
}

.cfg22nd{
    position: relative;
}

.cpreview2nd{
    width: 11rem;
    position: absolute;
    right: -40rem;
    height: auto;
}

.btn-uploadBook {
    position: absolute;
    left: 1rem;
    min-width: 11rem;
    background: #000;
    color: #fff;
    user-select: none;
    padding: 0.5rem 0rem;
    text-align: center;
    border-radius: 5px;
    font-family: Roboto Medium;
}
.failecd{
    position: absolute;
    right: -40rem;
    font-size: 1.5rem;
    width: 11rem;
    height: 11rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #ffffffdd;
    box-shadow: 0 0 20px #000;
}

.cpreview{
    width: 11rem;
    position: absolute;
    right: -40rem;
    height: auto;
}

.btn-upc{
    outline: none;
    border:none;
    background: #070;
    color: #fff;
    padding: 0.5rem 2rem;
    border-radius: 5px;
    font-family: Roboto Medium;
}
.btn-upc:hover{
    background: #030;
}

.file_info{
    width: 25rem;
    border: 2px solid #000;
    margin-top: 2rem;
    padding: 1rem;
    border-radius: 20px;
}

.file_info h1{
    margin-bottom: 1rem;
    font-size: 2rem;
}

.file_info p{
    margin-bottom: 1rem;
    font-size: 1rem;
}

.btn-filee{
    position: absolute;
    left: 1rem;
    min-width: 11rem;
    background: #000;
    color: #fff;
    user-select: none;
    padding: 0.5rem 0rem;
    text-align: center;
    border-radius: 5px;
    font-family: Roboto Medium;
}

.fs{
    position: absolute;
    left: 14rem;
    min-width: 11rem;
    font-size: 1.4rem;
}

.createIcon{
    position: fixed;
    bottom: 2rem;
    right: 3rem;
    background: #4F5EFE;
    font-family: Roboto Medium;
    color: #fff;
    user-select: none;
    border-radius: 50%;
    font-size: 2rem;
    width: 6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 9px #000;
    font-size: 5.5rem;
    height: 6rem;
}

.createOv{
    background: #fff;
    opacity: 0.9;
    width: 100vw;
    height: 100vw;
    position: fixed;
    user-select: none;
    top: 0;
    left: 0;
    z-index: 999999999999999999;
}

#createBook{
    bottom: 2rem;
    user-select: none;
    right: 3rem;
    position: fixed;
    bottom: 2rem;
    right: 3rem;
    background: transparent;
    font-family: Roboto Medium;
    color: #fff;
    z-index: 999999999999999999;
    font-size: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.createBookIcon{
    bottom: 2rem;
    right: 3rem;
    position: fixed;
    bottom: 2rem;
    right: 3rem;
    background: #f00;
    font-family: Roboto Medium;
    color: #fff;
    border-radius: 50%;
    z-index: 999999999999999999;
    font-size: 2rem;
    width: 6rem;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 9px #000;
    font-size: 5.5rem;
    height: 6rem;
}

#createBook img{
    width: 4rem;
}

#createBook #text{
    font-size: 2rem;
    right: 8rem;
    width: 15rem;
    position: absolute;
    transform: translateY(-50%);
    text-align: center;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    margin-top: -2rem;
    color: #607D8B;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
    background: #fff;
}


#createBookList{
    position: fixed;
    bottom: 10rem;
    right: 4rem;
    user-select: none;
    background: transparent;;
    font-family: Roboto Medium;
    color: #fff;
    z-index: 999999999999999999;
    font-size: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.createBookListIcon{
    bottom: 2rem;
    right: 3rem;
    position: fixed;
    bottom: 10rem;
    right: 4rem;
    background: #fff;
    font-family: Roboto Medium;
    color: #fff;
    border-radius: 50%;
    z-index: 999999999999999999;
    font-size: 2rem;
    width: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 9px #000;
    font-size: 5.5rem;
    height: 5rem;
}

#createBookList img{
    width: 3rem;
}

#createBookList #text2{
    font-size: 2rem;
    right: 8rem;
    width: 16rem;
    position: absolute;
    transform: translateY(-50%);
    text-align: center;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    margin-top: -2rem;
    color: #607D8B;
    box-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
    background: #fff;
}

.btn-ptf{
    outline: none;
    border:none;
    background: #007;
    color: #fff;
    padding: 0.5rem 2rem;
    border-radius: 5px;
    font-family: Roboto Medium;
}

.btn-ptf:hover{
    background: #003;
}

@media (max-width: 1024px) {
    .cfg{
        flex-direction: column;
    }
    .cfg2nd{
        flex-direction: column;
    }
    .btn-ptf{
        margin-top: 2rem;
    }
    .createIcon{
        z-index: 9999999999999999999999999999999999999999999999;
        width: 4rem;
        height: 4rem;
        font-size: 3.5rem;
        bottom: 4.5rem;
        right: 1.5rem;
    }
    .btn-filee{
        min-width: 7rem;
        font-size: 0.8rem;
    }
    #createBook{
        z-index: 9999999999999999999999999999999999999999999999;
        bottom: 5rem;
    }
    #createBook .createBookIcon{
        bottom: 5rem;
        width: 4rem;
        height: 4rem;
        right: 1.5rem;
    }
    .btn-uploadBook{
        min-width: 7rem;
        font-size: 0.8rem;
    }
    .file_info{
        width:15rem;
        border: 2px solid #000;
        margin-top: 2rem;
        padding: 1rem;
        border-radius: 20px;
    }

    .file_info h1{
        margin-bottom: 1rem;
        font-size:1.3rem;
    }

    .file_info p{
        margin-bottom: 1rem;
        font-size: 0.8rem;
    }
    #createBook img{
        width: 2.5rem;
    }
    #createBook #text{
        font-size: 0.95rem;
        width: 9rem;
        right: 5rem;
        margin-top: -0.7rem;
    }
    .cpreview{
        margin-top: 4rem;
        left: 0;
    }
    .btn-upc{
        margin-top: 20rem;
        min-width: 6rem;
    }
    .fs{
        font-size: 0.8rem;
        left: 9rem;
        min-width: 15rem;
    }
    .fs:last-child{
        min-width: 5rem;
        max-width: 7rem;
        height: auto;
        word-wrap: break-word;
    }
    #createBookList{
        z-index: 9999999999999999999999999999999999999999999999;
        bottom: 11rem;
    }
    #createBookList .createBookListIcon{
        bottom: 11.5rem;
        width: 3rem;
        height: 3rem;
        right: 2rem;
    }
    #createBookList img{
        width: 1.5rem;
    }
    #createBookList #text2{
        font-size: 0.95rem;
        width: 9rem;
        right: 4rem;
        margin-top: -0.7rem;
    }
    .createOv{
        z-index: 9999999999999999999999999999999999999999999999;
        height: 100vh;
    }
}

