.successc-modal-container{
    width: 100%;
    height: 100vh;
    margin-top: -12%!important;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    margin: 0;
    background-color: rgba(0, 0,0, 0.96);
    margin: 0;
    z-index: 100000000000;
}
.successc-modal{
    width: 40vw;
    box-shadow: 0 0 20px #bbb;
    padding: 3vw;
    text-align: center;
    position: relative;
    background: #fff;
    border-radius: 5px;
}

.successc-modal .icon{
    font-size: 10vw;
    text-shadow: 0 0 20px #000;
    color: #0a0;
    margin-bottom: 2vw;
}

.successc-modal h1{
    font-family: Montserrat Bold;
    /*margin-bottom: 2vw;*/
    font-size: 2.5vw;
}


.successc-modal h2{
    font-family: Montserrat Bold;
    margin-bottom: 2vw;
    font-size: 1.5vw;
}

.insert_contents{
    padding: 1.5vw 1.5vw;
    background: #00f;
    font-family: "Raleway Bold"!important;
    color: #fff;
    font-size: 1.5vw;
    border-radius: 4px;
    outline: none;
    border: none;
}


.successc-modal div{
    display: flex;
    width: 100%;
    justify-content: space-between;
    flex-direction: column;
}

.successc-modal .link{
    color: #000;
    font-size: 1.5vw;
    color: #00f;
    text-decoration: none;
}

.successc-modal .link:hover{
    color: #00a;
}

.insert_contents:hover{
    background: #00a;
    text-decoration: none;
}


@media (max-width: 1100px){
    .successc-modal{
        transform: scale(2);
    }
}
