.book_page{
    display: flex;
    width: 80vw;
    justify-content: space-between;
}

.book{
    width: 52.5vw;
    height: 13vw;
    background: #fff;
    padding: 0.5vw;
    margin-bottom: 7px;
    position: relative;
}

.pagalHain{
    cursor: default;
}

.book_cover{
    max-width: 8vw;
    max-height: 12vw;
    margin-right: 1vw;
    position: absolute;
    left: 0.5vw;
}

.rightSection{
    position: absolute;
    left: 9.5vw;
}

.book_title{
    font-family: "Cherry Swash";
    font-size: 2.1vw;
    margin-bottom: 0.5vw;
    color: #000;
}

.total_section{
    display: flex;
    width: 28vw;
    justify-content: space-between;
    align-items: center;
}

.total_section h6{
    font-family: Poppins Regular;
    margin-bottom: 0.6rem;
    color: #000;
    font-size: 0.95vw!important;
}

.description{
    font-family: Mulish ExtraLight;
    font-weight: 508;
    letter-spacing: 0.05px;
    font-size: 1.15vw;
    color: #000;
    width: 40vw;
    margin-bottom: 0.1vw;
}

.PDF{
    width: 3vw;
    border-radius: 50%;
    position: absolute;
    right: 0.5vw;
}

.HTML{
    width: 3vw;
    position: absolute;
    right: 0.5vw;
}

.faviorate{
    width: 2.5vw;
    height: 2.5vw;
    background: #f00;
    clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
}

.footerleft{
    display: flex;
    align-items: center;
}

.footerleft{
    margin-left: 5vw;
    width: 18vw;
    display: flex;
    position: absolute;
    bottom: 1vw;
    right: 20vw;
    justify-content: space-between;
}

.footerleft img{
    cursor: pointer;
    height: 2.2vw;
}

.books_container{
    padding-left: 15vw;
}

.footerright{
    position: absolute;
    right: 0.5vw;
    bottom: 0.5vw;
}

.footerright img{
    cursor: pointer;
    height: 4vw;
}

@media (max-width: 1024px){
    .books_container{
        padding-top: 6rem;
        padding-left: 0vw;
    }
    .book{
        width: 100vw;
        transform: scale(1);
        padding: 0.5rem;
        height: 34vw;
        z-index: 1;
        border-top: 1px solid rgba(0, 0, 0, 0.6);
        margin-bottom: 0;
    }
    .book_cover{
        max-width: 20vw;
        max-height: 28vw;
    }
    .book_title{
        font-size: 0.9rem;
        font-family: Roboto Medium;
        margin-top: 0.3rem;
        margin-bottom: 0.4rem;
    }
    .total_section{
        width: 53vw;
    }
    .total_section h6{
        margin-bottom: 0.6rem;
        font-family: Roboto Medium;
        font-size: 2vw!important;
    }
    .description{
        font-size: 2.5vw;
        width: 60vw;
        font-weight: 600;
        margin-bottom: 0.3rem;
    }
    .menu_bar_contents{
        width: 100%;
        margin: 0;
    }
    .rightSection{
        top: 0vw;
        left: 23vw;
    }
    .footerleft{
        right: 30vw;
        width: 40vw;
        top: 23vw;
    }
    .footerleft img{
        height: 6vw;
    }
    .footerright img {
        height: 12vw;
    }
    .faviorate{
        width:7vw;
        height: 7vw;
    }
    .book_page{
        margin-bottom: 10vw!important;
        width:100vw;
    }
    .PDF{
        width: 5vw;
    }
    .HTML{
        width: 5vw;
    }
}

@media (min-width: 1025px) {
    .book{
        border: 1px solid rgba(0, 0, 0, 0.3);
        border-bottom: none;
        margin-bottom: 0px;
    }
}