.main{
    background: #045595;
    color: #fff;
    width: 100%;
    display: flex;
    margin-top: 10vh;
    padding: 2vh 0;
    justify-content: space-around;
    align-items: center;
    transition: 0.2s linear all;
}

.createbtn{
    margin: 0;
    border: none;
    outline: none;
    background: #0f0;
    font-family: Raleway Bold;
    font-weight: bolder;
    border-radius: 5px;
    text-align: left;
    padding: 2vh 1.5vw;
    position: relative;
    font-size: 1.5vw;
}

.createbtn:hover{
    background: #0e0;
    transition: 0.5s;
}


.createbtn .i{
    font-size: 2vw;
    padding-left: 10px;
}

.home_page h1{
    margin-bottom: 15px;
    font-family: "Montserrat Bold";
    font-size: 3.3vw;
}

.home_page h2{
    font-family: "Montserrat Bold";
    font-size: 2vw;
    margin-bottom: 15px;
    padding-left: 8px;
}

.videos video{
    box-shadow: 0 0 30px #4F5EFE;
    width: 35vw;
}

.videos h1{
    font-family: "MontserratA Regular";
    font-size: 2.94vw;
}

.videos h2{
    font-family: "MontserratA Regular";
    font-size: 2.34vw;
}

.videos div{
    display: flex;
    justify-content: space-between;
    position: relative;
    width: 48%;
}
.videos div h2{
    font-family: "MontserratA Medium";
    position: absolute;
    bottom: 0;right: 0;
}

@media (max-width: 1024px){
    .main{
        height: auto;
        min-height: 90vh;
        flex-direction: column;
    }
    .videos{
        height: 60vh!important;
    }
    .videos video {
        width: 90vw;
    }
    .videos h1{
        font-size: 7vw;
    }
    .videos h2{
        font-size: 5.5vw;
    }

    .videos div h2{
        font-size: 6vw;
    }
    .home_page{
        width: 90vw;
        text-align: center;
    }
    .home_page h1{
        font-size: 1.29rem;
    }
    .home_page h2{
        padding-left: 0.2rem;
        font-size: 1rem;
    }
    .createbtn{
        font-family: "Raleway ExtraBold";
        font-size: 1rem;
    }
    .createbtn .i{
        font-size: 1rem;
    }
}

@media (min-width: 1025px){
    .main{
        height: 90vh;
        flex-direction: row-reverse;
    }
}

.success-modal-container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    margin: 0;
    background-color: rgba(0, 0,0, 0.96);
    margin: 0;
    z-index: 100000000000;
}
.success-modal{
    width: 40vw;
    box-shadow: 0 0 20px #bbb;
    padding: 3vw;
    text-align: center;
    position: relative;
    background: #fff;
    border-radius: 5px;
}

.success-modal .icon{
    font-size: 10vw;
    text-shadow: 0 0 20px #000;
    color: #0a0;
    margin-bottom: 2vw;
}

.success-modal h1{
    font-family: Montserrat Bold;
    /*margin-bottom: 2vw;*/
    font-size: 2.5vw;
}


.success-modal h2{
    font-family: Montserrat Bold;
    margin-bottom: 2vw;
    font-size: 1.5vw;
}

.visit_dashboard_btn{
    padding: 0.5vw 1.5vw;
    background: #0a0;
    font-family: "Raleway Bold"!important;
    color: #fff;
    font-size: 1.5vw;
    border-radius: 4px;
    outline: none;
    border: none;
}

.ok_btn{
    padding: 0.5vw 1.5vw;
    background: #00f;
    color: #fff;
    outline: none;
    font-family: "Raleway Bold"!important;
    border: none;
    border-radius: 4px;
    font-size: 1.5vw;
}

.ok_btn{
    color: #fff;
    text-decoration: none;
}

.success-modal div{
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.visit_dashboard_btn:hover{
    color: #fff;
    text-decoration: none;
}

@media (max-width: 1100px){
    .success-modal{
        transform: scale(2);
    }
}

@media (max-width: 441px){
    .errmsg{
        transform: scale(0.9)!important;
    }
}


@media (max-width: 397px){
    .errmsg{
        transform: scale(0.8)!important;
    }
}

@media (max-width: 350px){
    .errmsg{
        transform: scale(0.7)!important;
    }
}

@media (max-width: 303px){
    .errmsg{
        transform: scale(0.6)!important;
    }
}


@media (max-width: 273px){
    .errmsg{
        transform: scale(0.58)!important;
    }
}



@media (max-width: 260px){
    .errmsg{
        transform: scale(0.55)!important;
    }
}
