.LoginMain{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    overflow-x: hidden;
    margin: 0;
}

.errmsg{
    margin-top: 10vh;
}

.ak3_form{
    color: #fff;
    padding: 35px;
    background: linear-gradient(60deg, #06a398, #000098);
    border-radius: 4px;
    margin: auto;
}

.ak3_form h1{
    padding-bottom: 2rem;
}

.ml-ak3{
    margin-left: 80%;
}

.bg-clickable{
    width: 23rem;
    background: transparent;
    color: #000;
    transition: 0.3s linear!important;
}

.bg-clickable:focus, .bg-clickable:valid{
    background: #fff;
    color: #000;
}

.danger{
    color: #f00;
    background: #fff;
}

@media (max-width: 455px){
    .ak3_form{
        transform: scale(0.9);
        margin: 0;
    }
    .form-lab{
        font-size: 6vw;
    }
    .ml-ak3{
        transform: scale(1.3);
        margin-top: 4%!important;
    }
}


@media (max-width: 412px){
    .ak3_form{
        transform: scale(0.8);
    }
}


@media (max-width: 370px){
    .ak3_form{
        transform: scale(0.78);
    }
}

@media (max-width: 350px){
    .ak3_form{
        transform: scale(0.7);
    }
}

@media (max-width: 315px){
    .ak3_form{
        transform: scale(0.68);
    }
}

@media (max-width: 303px){
    .ak3_form{
        transform: scale(0.6);
    }
}

@media (max-width: 275px){
    .ak3_form{
        transform: scale(0.58);
    }
}

@media (max-width: 260px){
    .ak3_form{
        transform: scale(0.5);
    }
}

@media (max-width: 230px){
    .ak3_form{
        transform: scale(0.48);
    }
}