#sortBy{
    background: #000;
    padding: 0.5vw 1vw;
    margin-bottom: 4vw!important;
    margin-left: 0.5vw;
    width: 15vw;
    color: #fff;
    display: inline;
    border-radius: 5px;
    position: relative;
}

#sortBy label{
    margin-right: 0.5vw;
    font-family: "Poppins SemiBold";
    font-size: 1.1vw;
    margin-left: 0vw;
    margin-bottom: 0;
}

#sortBy select{
    background: transparent;
    color: #fff;
    width: 10vw;
    height: 4vh;
    z-index: 99999999;
    outline: none;
    border: none;
    -webkit-appearance: none;
    text-align: center;
    -moz-appearance: none;
    font-size: 1.1vw;
    font-family: "Poppins Medium";
}

#sortBy option{
    background: #fff;
    color: #000;
}

#sortBy div{
    z-index: 99;
    background: #fff;
    width: 1vw;
    top: 50%;
    position: absolute;
    right: 0.3vw;
    transform: translateY(-50%);
    height: 0.5vw;
    clip-path: polygon(0 0, 50% 100%, 100% 0%);
}

#bookType{
    margin-bottom: 0.5vw;
    position: relative;
    display: inline;
}

#bookType label{
    margin-right: 1vw;
    font-family: "Poppins Regular";
    font-size: 1vw;
    color: #000;
}

#bookType select{
    background: #eee;
    color: #000;
    outline: none;
    position: relative;
    box-shadow: 0 0 2px #000;
    padding: 0.5vw 1vw;
    border: none;
    -webkit-appearance: none;
    z-index: 9;
    width: 8vw;
    -moz-appearance: none;
    font-size: 0.8vw;
    font-family: "Poppins Medium";
}

#bookType option{
    background: #eee;
    color: #000;
}

#bookType div{
    z-index: 99;
    background: #000;
    width: 1vw;
    top: 50%;
    position: absolute;
    right: 0.3vw;
    transform: translateY(-50%);
    height: 0.5vw;
    clip-path: polygon(0 0, 50% 100%, 100% 0%);
}

#bookList{
    margin-bottom: 0.5vw;
    position: relative;
}

#bookList label{
    margin-right: 0.5vw;
    font-family: "Poppins Regular";
    font-size: 1vw;
    color: #000;
}

#bookList select{
    background: #eee;
    color: #000;
    margin-left: 5vw;
    outline: none;
    width: 11vw;
    box-shadow: 0 0 2px #000;
    padding: 0.5vw 1vw;
    border: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    font-size: 0.8vw;
    font-family: "Poppins Medium";
}

#bookList option{
    background: #eee;
    color: #000;
}

#bookList div{
    z-index: 99;
    background: #000;
    width: 1vw;
    top: 75%;
    position: absolute;
    right: 4.5vw;
    transform: translateY(-50%);
    height: 0.5vw;
    clip-path: polygon(0 0, 50% 100%, 100% 0%);
}

#dateCreatedOn{
    margin-bottom: 1vw;
}

#dateCreatedOn label{
    margin-right: 1.5vw;
    font-family: "Poppins Regular";
    font-size: 1vw;
    color: #000;
}

#dateCreatedOn input{
    padding: 0.5vw 1vw;
    margin-left: 8vw;
    font-size: 0.8vw;
}

.filters h4{
    font-size: 1.2vw;
    color: #000;
    font-family: "Poppins SemiBold";
}

.filters h5{
    font-size: 1.1vw;
    color: #443344;
    margin-bottom: 0.5vw;
    font-family: "Poppins Light";
}

.filters h3{
    font-size: 1.3vw;
    margin-bottom: 0vw;
    margin-top: 1vw;
    color: #000;
    font-family: "Poppins SemiBold";
}

.filter_btn{
    display: none;
}

@media (max-width: 1024px) {
    .filter_container.d{
        display: none;
    }
    .filters_ov.d{
        display: none;
    }
    .filter_btn{
        position: fixed;
        background: #fff;
        color: #000;
        top: 5rem;
        right: 1rem;
        outline: none;
        padding: 0.3rem 1rem;
        font-family: Roboto Medium;
        border-radius: 3px;
        box-shadow: 0 0 5px #000;
        border: none;
        z-index: 999999999;
        display: block;
    }
    .ov{
        width: 100vw;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.95);
    }
    .filter_container{
        width: 44vw;
        background: #fff;
        padding: 1vw;
        position: fixed;
        left: 50%;
        top: 30%;
        z-index: 99999999999;
        transform: translate(-50%);
        overflow-y: scroll;
        border-radius: 10px;
    }
    #sortBy{
        margin-bottom: 0vw;
        width: 100%;
    }
    .filters h4{
        font-family: "Roboto Medium";
        font-size: 2.8vw;
        margin-bottom: 0.5vw;
    }
    .filters h5{
        font-family: "Roboto Regular";
        margin-bottom: 0.5vw;
        font-size: 2.7vw;
    }
    .filters h3{
        font-family: "Roboto Medium";
        font-size: 2.6vw;
        margin-bottom: 0.5vw;
    }
    #sortBy{
        padding: 1vw 2vw;
        margin-bottom: 0.5vw;
        width: auto;
    }
    #sortBy label{
        font-size: 2.4vw;
        width: auto;
        font-family: "Roboto Regular";
    }
    #sortBy select{
        width: 20vw;
        font-size: 2.4vw;
        font-family: "Roboto Medium";
    }
    #sortBy div{
        width: 2.5vw;
        height: 1.3vw;
    }
    #bookType{
        margin-bottom: 0.5vw;
    }
    #bookType label{
        font-size: 2.4vw;
        font-family: "Roboto Regular";
    }
    #bookType select{
        width: 13vw;
        font-size: 2.4vw;
        font-family: "Roboto Medium";
    }
    #bookType div{
        width: 2.5vw;
        height: 1.3vw;
    }
    #bookList{
        margin-bottom: 0.5vw;
    }
    #bookList label{
        font-size: 2.4vw;
        font-family: "Roboto Regular";
    }
    #bookList select{
        font-size: 2.4vw;
        width: 25vw;
        font-family: "Roboto Medium";
    }
    #bookList div{
        width: 2.5vw;
        height: 1.3vw;
        right: 12.5vw;
    }
    #dateCreatedOn label{
        font-size: 2.4vw;
        font-family: "Roboto Regular";
    }
    #dateCreatedOn input{
        font-size: 2.4vw;
        font-family: "Roboto Medium";
    }
    .filters_ov{
        position: fixed;
        width: 100vw;
        height: 100vh;
        background: rgba(0, 0, 0, 0.95);
        margin-top: -6rem;
        z-index: 99999999999;
    }
    .ma{
        margin-top: -6rem;
        height: 100vh;
        width: 100vw;
    }
}

@media (max-width: 513px) {
    .filters h3{
        margin-bottom: -0.5vw;
    }
    #bookType{
        margin-bottom: -0.5vw;
    }
    #bookList{
        margin-bottom: -0.5vw;
    }
    #bookList label{
        margin-bottom: -0.5vw;
    }
    #dateCreatedOn label{
        margin-bottom: -0.5vw;
    }
}

@media (min-width: 1025px) {
    .filter_container{
        background: #fff;
        position: fixed;
        padding: 0.5vw;
        z-index: 999999;
        border: 1px solid rgba(0, 0, 0, 0.3);
        left: 73.5vw;
        width: 21vw;
        margin-top: 0.5vw;
    }
}