@import url("./css/NavigationBar.css");
@import url("./css/LandingPage.css");
@import url("./css/LoginPage.css");
@import url("./css/TopSection.css");
@import url("./css/Menu.css");
@import url("./css/Books.css");
@import url("./css/Filters.css");
@import url("./css/createBook.css");
@import url("./css/ABookPage.css");
@import url("./css/rbpdf.css");
@import url("./css/fonts.css");
@import url("./css/EditProfileForm.css");

*{
    scroll-behavior: smooth;
    transition: 0.2s linear all;
}


::selection{
    color: #fff;
    background: #00f;
}
::-moz-selection{
    color: #fff;
    background: #00f;
}

.center{
    width: 60vw;
    height: 30vw;
    background: rgba(0, 0, 0, 0.9);
    border-radius: 8px;
    background-size: 30vw 10vw;
    text-align: center;
    color: #fff;
    display: flex;
    z-index: 9;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.center h1{
    font-size: 9rem;
    font-family: Montserrat Bold;
}

.center h2{
    font-size: 4rem;
    font-family: Montserrat Bold;
}

.fp{
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.bg{
    position: absolute;
    z-index: 8;
    width: 100vw;
    height: 100vh;
    background: url("bg4.jpg");
    background-size: cover;
    filter: brightness(6);
}

@media (max-width: 1024px) {
    .loader h1{
        font-size: 1.6rem!important;
    }
    .center{
        width: 90vw;
        height: 50vw;
    }
    .center h1{
        font-size: 4rem;
    }
    .center h2{
        font-size: 2rem;
    }
}

::-webkit-scrollbar{
    width: 12px;
    background: #000;
}

::-webkit-scrollbar-thumb{
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    background: #4F5EFE;
}

::-webkit-scrollbar-thumb:hover{
    background: #3156bE;
}

::-moz-scrollbar{
    width: 12px;
    background: #000;
}

::-moz-scrollbar-thumb{
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
    border-bottom-left-radius: 10px;
    background: #4F5EFE;
}

::-moz-scrollbar-thumb:hover{
    background: #3156bE;
}

.loader{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: -50%;
    display: flex;
    justify-content: center;
    transform: translateX(50%);
    align-items: center;
    flex-direction: column;
    z-index: 99999999999999999999;
    background: rgba(0, 0, 0, 0.85);
}

.loader div{
    width: 5rem;
    margin-bottom: 1rem;
    border-radius: 50%;
    height: 5rem;
    border: 0.2rem solid #ccc;
    border-top-color: #4F5EFE;
    animation: spin 3s ease infinite;
}

.loader h1{
    font-family: Roboto Medium;
    color: #fff;
    font-size: 2.5rem;
    animation: space 1s ease infinite alternate;
}

@keyframes space {
    100%{
        letter-spacing: 0.5rem;
    }
}

@keyframes spin {
    100%{
        transform: rotate(360deg);
    }
}