.topSection{
    width: 100%;
    background: linear-gradient(to right, #4F5EFE, #4CAFA9);
    border-radius: 0% 10% 48% 52% / 0% 0% 100% 100%;
    z-index: 999999999999999;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 1.5vw;
    position: fixed;
}

#dropdown-basic{
    outline: none;
    border: none;
}

.dekho{
    position: fixed;
    z-index: 999999999;
    background: #fff;
    width: 100vw;
    height: 20%;
}

.topSection h1{
    color: #fff;
    font-family: Montserrat Bold;
    font-size: 2.5vw;
    text-shadow: 0 0 20px #666;
}

.topSection .search-bar{
    width: 30vw!important;
    padding:0.5vw!important;
    outline: none!important;
    border:none!important;
    border-radius: 0px!important;
    text-align: center!important;
    font-size: 1.3vw!important;
}

.userIcon{
    border: 1px solid #fff;
    width: 3rem;
    height: auto;
    border-radius: 50%;
    color: #000;
}

.main_user{
    position: absolute;
    right: 3.5vw;
}


@media (max-width: 1024px){
    .dekho{
        position: fixed;
        z-index: 999999999;
        background: #fff;
        width: 100vw;
        height:6rem;
    }
    .topSection{
        z-index: 999999999999999;
        width: 100%!important;
        background: linear-gradient(to right, #4F5EFE, #4CAFA9)!important;
        border-radius: 0% 10% 48% 52% / 0% 0% 100% 100%;
        display: flex!important;
        flex-direction: column!important;
        align-items: center!important;
        z-index: 999999999999999999999999999999999;
        padding-bottom: 3vw!important;
        height: 22vw;
        padding: 0.2rem;
        top: 0vw;
    }

    .topSection h1{
        color: #fff!important;
        font-family: Montserrat Bold!important;
        font-size: 4vw!important;
        margin-bottom: 1.5vw!important;
        text-shadow: 0 0 20px #666!important;
    }

    .topSection .search-bar{
        width: auto!important;
        padding:0vw 3vw!important;
        outline: none!important;
        border:none!important;
        text-align: center!important;
        font-size: 3vw!important;
    }
    .userIcon{
        width: 2.3rem;
        height: auto;
        margin-top: -0.3rem;
    }
    .main_user{
        position: absolute;
        right: 0.1rem;
        z-index: 99999999999999999999999999999999999999999999999999999999999999!important;
    }
}