.bg-na{
    background: #4F5EFE!important;
    height: 10vh;
    position: fixed!important;
    top: 0!important;
    left: 0!important;
    right: 0!important;
    width: 100vw!important;
    z-index: 9000000000000000000000;
}

.nav-b{
    font-family: "Montserrat SemiBold";
}

.nav-l{
    color: #eeee!important;
    font-family: "Raleway SemiBold";
    margin-right: 20px;
}

.nav-la{
    color: #fff!important;
    margin-right: 20px;
    font-family: "Raleway Bold";
}

.nav-l:hover{
    color: #efef!important;
    text-decoration: none;
}

@media (max-width: 1025px){
    .hdf{
        display: none!important;
    }
    .nav-l{
        font-size:4vw;
    }
    .nav-la{
        font-size:4vw;
    }
    .nav-b{
        font-size: 3.3vw;
    }
}


@media (min-width: 1025px){
    .navbar-nav{
        flex-direction: row;
    }
}

body{
    width: 100vw;
    overflow-x: hidden;
}