.bg-grey{
    background: #fff;
    min-height: 100vh;
}

.mainController{
    min-height: 100vh;
    padding-top: 12%;
}

.menu_bar_buttons{
    z-index: 999999;
    box-shadow: 0 0 20px #999;
    background: #fff;
    width: 8vw;
    display: flex;
    margin-left: 3.5vw;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-top-left-radius: 8vw;
    user-select: none;
    border-top-right-radius: 8vw;
    border-bottom-left-radius: 8vw;
    border-bottom-right-radius: 8vw;
}

.menu_bar_button{
    display: flex;
    justify-content: center;
    padding: 0.5vw;
    align-items: center;
    width: 8vw;
    color: #000;
    text-decoration: none;
    background: #fff;
    box-shadow: 0 0 2px #999;
    height: 7vw;
    flex-direction: column;
}

.menu_bar_button:hover{
    background: #efefef;
    color: #000;
    text-decoration: none;
}

.menu_bar_button:first-child{
    border-top-left-radius: 8vw;
    border-top-right-radius: 8vw;
}

.menu_bar_button:last-child{
    border-bottom-left-radius: 8vw;
    border-bottom-right-radius: 8vw;
}

.menu_bar_button.active{
    width: 8vw;
    background: #ccc!important;
}

.menu_bar_button_text{
    font-family: Roboto Medium;
    font-size: 1.05vw;
}

.menu_bar_button_icon{
    width: 4vw;
}

.content_title{
    font-size: 2vw;
    position: fixed;
    top: 20%;
    z-index: 999999;
    width: 52.5vw;
    margin-left: 15vw;
    background: #fff;
    margin-bottom: 1vw;
    font-family: Roboto Regular;
}
@media (max-width: 1024px) {
    .bg-grey{
        background: #fff;
    }
    .menu_bar_buttons{
        position: fixed;
        bottom: 0;
        flex-direction: row;
        left: 0;
        right: 0;
        height: 14.2216vw;
        margin-left: 0;
        width: 100%;
        border-top-left-radius: 0;
        border-top-right-radius: 0;
        border-bottom-left-radius: 0;
        border-bottom-right-radius: 0;
    }
    .books_container{
        margin-bottom: 15vw;
        padding-top: 4.1rem;
    }

    .menu_bar_button:first-child{
        border-top-left-radius: 0vw;
        border-top-right-radius: 0vw;
    }

    .menu_bar_button:last-child{
        border-bottom-left-radius: 0vw;
        border-bottom-right-radius: 0vw;
    }
    .menu_bar_button{
        flex: 1 1 100vw;
        height: 17.777vw;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .menu_bar_button_icon{
        width: 8.5vw;
    }
    .menu_bar_button_text{
        font-family: "Roboto Medium";
        font-size: 3vw;
        margin-top: 1vw;
    }
    .mainController{
        display: block;
        z-index: 999999;
        width: 100vw;
    }
    .content_title{
        font-family: "Roboto Regular";
        font-size: 0.9rem;
        margin-left: 0vw;
        padding-left: 0.6rem;
        position: fixed;
        background: #fff;
        z-index: 99999;
        height: 3rem;
        z-index: 999999999;
        width: 100vw;
        top: 24vw;
        margin-bottom: 0.8rem;
    }
}

@media (min-width: 1025px) {
    .menu_bar_buttons{
        position: fixed;
        top: 55%;
        transform: translateY(-50%);
    }
}

img{
    user-select: none;
}